.flex-container {
    display: flex;
    margin: 20px;
}

.flex-child {
    flex: 1;
    border: 2px solid ;
    border-color: #ccc;
}  

.flex-child:first-child {
    margin-right: 5px;
}   

#form-div{
    padding-left: 20px;
    padding-right: 20px;
}

input[type=text],input[type=email],textarea[type=text], select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  input[type=submit] {
    width: 100%;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  input[type=submit]:hover {
    background-color: #45a049;
  }
  
  form_class {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
  }